var binary = Uint8Array.from(atob(""), c => c.charCodeAt(0));

// @ts-expect-error this will be compiled to ArrayBuffer
const wasmBinary = binary;
const getWasmInstance = async (info) => {
    return WebAssembly.instantiate(wasmBinary, info).then(wasm => wasm.instance.exports);
};

export { getWasmInstance as default, getWasmInstance, wasmBinary };
